.startmeter .gauge-data{
	z-index: 4;
	color: rgba(255,255,255,.2);
	font-size: 1.5em;
	line-height: 25vmin;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 50%;
	margin-left: auto;
	margin-right: auto;
	-webkit-transition: all 1s ease-out;
	transition: all 1s ease-out;
}

