.startmeter{
	width:56vmin;
	height:28vmin;
	/* width:56px;
	height:28px; */
	position: relative;
	overflow: hidden;
    text-align: center;
    margin: auto;
	/* transform: translate(-50%, -50%); */
}

.startmeter .gauge-a{
	 z-index: 1;
	/* position: absolute; */
	background-color: #419c59;
	background: #DCE35B;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #45B649, #DCE35B);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #45B649, #DCE35B); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
width: 100%;
height: 100%;
/* top: 0%; */
border-radius:250vmin 250vmin 0vmin 0vmin ;
/* border-radius:250px 250px 0px 0px ; */
}


.startmeter .gauge-b{
	z-index: 3;
	position: absolute;
	background-color: #222;
	background: none;
	width: 75%;
	height: 75%;
    /* top: 75%; */
    bottom: 0;
	margin-left: 50%;
    margin-right: auto;
    transform: translateX(-50%);
	border-radius:250vmin 250vmin 0vmin 0vmin ;
	/* border-radius:250px 250px 0px 0px ; */
}
.startmeter .gauge-c{
	z-index: 2;
	position: absolute;
	background-color: #000;
	background: #ED213A;  /* fallback for old browsers */
	background: -webkit-linear-gradient(to right, #93291E, #ED213A);  /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(to right, #93291E, #ED213A); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
	
	opacity: 0.9;
	width: 100%;
	height: 100%;
	/* top: 200px; */
	margin-left: auto;
	margin-right: auto;
	border-radius:0vmin 0vmin 200vmin 200vmin ;
     transform-origin:center top; 
     transition: all 1.3s ease-in-out; 
     /* transform: rotate(180deg); */
}


